import { post } from "../api/utils";
export default {
  log: function log(app, page) {
    if (typeof window.trackOnGA == 'function') {
      window.trackOnGA(app + '/' + page);
    }
  },
  convert: function convert(app, page) {
    post('/desktop/log_analytics', {
      page: app + '/' + page
    });
  }
};